.app {
  text-align: center;
}

#bg {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  background-position: center center;
  background-size: cover;
  transform: translateZ(0);
  will-change: transform;
}

.banner {
  align-items: center;
  background-color: #eec901;
  background-repeat: no-repeat;
  background-size: auto 100%;
  display: flex;
  height: 50px;
  justify-content: flex-end;
  margin-bottom: 5px;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
}

.banner .btn {
  background: none;
  border: none;
  color: black;
  font-size: 30px;
  line-height: 30px;
  height: 30px;
  margin-right: 5px;
  padding: 0;
  width: 30px;
}

.banner .btn .glyphicon {
  position: static;
}

.banner-placeholder {
  height: 55px;
}

.main {
  border: none;
}

.main,
.main .panel {
  background: none;
}

.nameko-panel {
  background: none;
}

/* override Button */
.btn:focus {
  outline: none !important;
}

/* override Panel */

.panel {
  margin: 0 5px 5px;
}

.panel:first-child {
  margin-top: 5px;
}

.panel-heading {
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.panel-body {
  padding: 5px;
}

/* override Modal */

.modal {
  display: flex !important;
}

.modal-dialog {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
  overflow: auto;
  margin: auto;
  max-height: 90%;
  max-width: 90%;
}

.modal-content {
  display: flex;
  flex-direction: column;
}

.modal-body {
  overflow-x: hidden;
  overflow-y: scroll;
  padding: 10px;
  text-align: center;
}

.modal-footer {
  text-align: center;
}

/* Tunnel & Tunnel Modal */

.tunnel-banner .panel-heading {
  background-size: cover;
  background-position: center;
}

.tunnel-banner.selectable > .panel-heading:hover,
.tunnel > .panel-heading:hover {
  cursor: pointer;
}

.tunnel-banner > * > .panel-body {
  padding-left: 20%;
}

.tunnel-banner .label,
.tunnel .label {
  background-color: rgba(0,0,0,0.5);
  font-size: 18px;
  overflow: auto;
  padding: .2em .2em .3em;
  text-overflow: ellipsis;
}

.tunnel-banner .label.check,
.tunnel .label.check {
  color: green;
  display: inline-block;
  height: 28px;
  width: 28px;
}

.memo {
  margin-top: 1px;
  border: 1px solid black;
}

/* Nameko */

.all-block {
  margin-left: -5px;
  margin-right: -5px;
}

.simple-nameko {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
}

.simple-nameko .img {
  max-height: 40px;
  max-width: 100%;
}

.nameko .check {
  left: 2px;
  position: absolute;
  top: 4px;
  height: 20px;
}

.nameko .name,
.simple-nameko .name {
  background-color: rgba(0,0,0,0.5);
  border-radius: 3px;
  color: white;
  font-size: 12px;
  padding: 2px 5px;
  position: relative;
}

.nameko-wrapper {
  padding: 0;
}

.nameko {
  background-color: rgba(249,241,217,0.7);
  border-radius: 4px;
  border-color: #ccc;
  border: 1px solid #ccc;
  margin: 2px;
  padding: 2px;
}

.nameko .name {
  padding: 0 3px;
  border: 2px solid transparent;
}

.nameko.selected .name {
  border-color: #58ff64;
}

.nameko.clickable:active,
.nameko.clickable:focus,
.nameko.clickable:hover {
  background-color: rgba(249,241,217,1);
  cursor: pointer;
}

.nameko .simple-nameko {
  flex: 1;
  flex-direction: column-reverse;
  justify-content: center;
}

.top-ctn {
  display: flex;
  margin-top: 2px;
}

.skill-ctn {
  height: 22px;
  line-height: 22px;
  margin: 0 -4px;
}
@media(min-width:768px) {
  .skill-ctn {
    height: 30px;
    line-height: 30px;
  }
}

.skill {
  background-color: transparent;
  background-position: center;
  background-size: cover;
  border: none;
  display: inline-block;
  vertical-align: middle;
  height: 18px;
  width: 18px;
}
@media(min-width:768px) {
  .skill {
    height: 24px;
    margin: 0 2px;
    width: 24px;
  }
}

.skill.unselected {
  filter: grayscale(100%);
  opacity: 0.5;
}

.value-ctn {
  display: flex;
  flex-direction: column;
  width: 40px;
}
@media(min-width:768px) {
  .value-ctn {
    line-height: 24px;
    width: 60px;
  }
}

.value {
  background-position: 0 center;
  background-repeat: no-repeat;
  background-size: 11px;
  margin-left: 1px;
  padding: 1px 1px 1px 12px;
}
@media(min-width:768px) {
  .nameko .value {
    background-size: 22px;
    font-size: 16px;
    margin: 0 1px 0 2px;
    padding: 2px 2px 2px 24px;
  }
}

.value-area {
  display: flex;
  justify-content: center;
  text-align: center;
}

.value-area > * {
  display: flex;
  flex-direction: column;
  margin: 0 5px;
}

.value-area .btn {
  background-color: lightgray;
  border-width: 0;
  border-radius: 50%;
  height: 44px;
  padding: 0;
  width: 44px;
}

.value-area .value {
  background-size: 22px;
  font-size: 16px;
  margin: 5px auto;
  padding: 2px 2px 2px 24px;
}

.nameko .btn {
  background: none;
  border: 1px solid #5a391e;
  color: #5a391e;
  margin: 3px 0;
  padding: 3px 6px;
}
.nameko .btn:hover {
  background: rgba(0,0,0,0.2);
}
@media(min-width:768px) {
  .nameko .btn {
    padding: 6px 12px;
  }
}

.nameko .btn .glyphicon {
  margin-right: 3px;
}

/* Tunnel Modal & Nameko Modal */

.tunnel-modal .modal-body,
.nameko-modal .modal-body {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.tunnel-modal .modal-header,
.nameko-modal .modal-header {
  background: linear-gradient(#5a391e, black);
  border: none;
  padding: 5px 15px;
}

.tunnel-modal .header-inner,
.nameko-modal .header-inner {
  background: linear-gradient(#808834, black);
  border-radius: 20px;
  color: white;
  display: flex;
  padding: 5px;
}

.tunnel-modal .header-inner .text,
.nameko-modal .header-inner .text {
  line-height: 22px;
}

.tunnel-modal .modal-footer,
.nameko-modal .modal-footer {
  background: linear-gradient(#5a391e, black);
  padding: 15px;
}

.tunnel-modal .modal-footer .btn,
.nameko-modal .modal-footer .btn {
  background: none;
  border-color: #f9f1d9;
  color: #f9f1d9;
}

/* Nameko Modal */

.nameko-modal .header-inner .img {
  margin-right: 5px;
}

.nameko-modal .header-inner .img,
.nameko-modal .header-inner .img img {
  width: 40px;
}

.nameko-modal .header-inner .img img:first-child {
  margin-top: -10px;
  margin-bottom: -20px;
  position: relative;
  z-index: 1;
}

.nameko-modal .header-inner .img img:last-child {
  margin-bottom: -10px;
}

.nameko-modal .modal-body {
  padding: 0;
}

.nameko-modal .content {
  padding: 10px;
  width: 100%;
}

.nameko-modal .buttons {
  display: flex;
  padding-bottom: 10px;
}

.nameko-modal .buttons .btn {
  background: none;
  border-color: #f9f1d9;
  color: #f9f1d9;
}

.nameko-modal .editor {
  align-items: center;
  background-color: rgba(255,255,255,0.5);
  flex-direction: column;
  justify-content: center;
  position: absolute;
  left: 0;
  top: 65px;
  bottom: 65px;
  right: 0;
}

.nameko-modal .editor .simple-nameko .img {
  max-height: 80px;
}

.nameko-modal .editor .simple-nameko .name {
  font-size: 16px;
}

.nameko-modal .editor .btn {
  box-shadow: 1px 1px 2px 2px rgba(0,0,0,0.3);
}

.nameko-modal .editor .skill-area .btn {
  background: none;
  border: none;
  padding: 0;
  margin: 6px 12px;
}

.nameko-modal .editor .skill {
  width: 30px;
  height: 30px;
}
@media(min-width:768px) {
.nameko-modal .editor .skill {
  width: 48px;
  height: 48px;
}
}

.nameko-modal .editor .notice {
  margin-top: 10px;
}

/* Result Panel */

.success,
.fail {
  height: 36px;
  width: 36px;
}

.reward {
  background-size: 36px;
  background-position: 2px center;
  background-repeat: no-repeat;
  display: inline-block;
  padding: 0 2px;
}

.reward img {
  height: 54px;
  width: 54px;
  margin: -9px;
}

.reward span.treasure {
  color: blue;
}

.reward span.item {
  color: #f44336;
}

.result-panel {
  text-align: left;
}

.result-panel .panel {
  padding: 2px;
}

.result-panel h1,
.result-group {
  position: sticky;
  margin: 0;
}

.result-panel h1 {
  color: #f9f1d9;
  background-color: #5a391e;
  font-size: 18px;
  line-height: 30px;
  overflow: hidden;
  padding: 3px 5px;
  text-overflow: ellipsis;
  top: 50px;
  white-space: nowrap;
  z-index: 2;
}

.result-group {
  background-color: #f9f1d9;
  border: 5px solid #5a391e;
  color: #5a391e;
  top: 86px;
  z-index: 1;
  margin-bottom: 5px;
  padding-left: 3px;
}

.result.panel {
  background-color: rgba(249, 241, 217, 0.7);
}

.result.panel:first-child {
  margin-top: 5px;
}

.result .fail {
  margin-right: -18px;
}

.result .namekos {
  padding-bottom: 2px;
}

.result .namekos > * {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
}

.result .simple-nameko {
  margin: 0 2px;
}

.result .skill {
  height: 36px;
  width: 36px;
}

.footer-placeholder {
  height: 69px;
}

.to-top > * {
  display: block;
}

.to-top img {
  max-height: 40px;
}

/* Run */

.footer {
  bottom: 20px;
  left: 50%;
  position: fixed;
  transform: translate(-50%,0);
  width: fit-content;
  z-index: 100;
}

.footer > * {
  margin: 3px 5px 0 5px;
}

.footer .btn {
  box-shadow: 1px 1px 2px 2px rgba(0,0,0,0.3);
}

#sort .glyphicon {
  margin-left: 6px;
}

#sort .caret {
  display: none;
}

#run .glyphicon {
  margin-right: 6px;
}
